const getWidth = () => window.innerWidth;

export const Featured = () => {
  const [state, setState] = useState({
    translate: 0,
    transition: 0.45,
    activeIndex: 0
  });

  const { translate, transition, activeIndex } = state;

  const nextSlide = () => {
    if (activeIndex === 2) {
      return setState({
        ...state,
        translate: 0,
        activeIndex: 0
      });
    }

    setState({
      ...state,
      activeIndex: activeIndex + 1,
      translate: (activeIndex + 1) * getWidth()
    });
  };

  const prevSlide = () => {
    console.log('entered');
    if (activeIndex === 0) {
      return setState({
        ...state,
        translate: 2 * getWidth(),
        activeIndex: 0
      });
    }

    setState({
      ...state,
      activeIndex: activeIndex - 1,
      translate: (activeIndex - 1) * getWidth()
    });
  };

  const [property, setProperty] = useState(featureds[0]);

  return (
    <>
      <div className='w-full px-6 md:px-8 lg:px-10 xl:px-0 xl:max-w-8xl mx-auto relative mt-8'>
        <div>
          <h3
            style={{ fontFamily: 'airbnb-medium' }}
            className='text-gray-750 text-2xl'>
            Featured Airbnb Plus destinations
          </h3>
        </div>
        <div className='pb-8'>
          <p
            style={{ fontFamily: 'airbnb-book' }}
            className='text-gray-750 text-sm'>
            Browse beautiful places to stay with all the comforts of home, plus
            more
          </p>
        </div>
        <div className='w-full h-80 relative overflow-hidden bg-red-550'>
          <SliderContent
            translate={translate}
            transition={transition}
            width={getWidth() * 3}>
            <Slide1 />
            <Slide2 />
            <Slide3 />
            <Slide4 />
            <Slide5 />
            <Slide6 />
          </SliderContent>
        </div>
        <Arrow direction='left' handleClick={prevSlide} />
        <Arrow direction='right' handleClick={nextSlide} />
      </div>
    </>
  );
};

const SliderContent = styled.div`
  transform: translateX(-${(props) => props.translate}px);
  transition: transform ease-out ${(props) => props.transition}s;
  height: 100%;
  width: ${(props) => props.width}px;
  display: flex;
`;

